export default {
  name: "SidemenuEntry",
  props: {
    children: Array,
    title: String,
    rail: Boolean,
    tooltip: [String, Object],
    value: Boolean
  },
  emits: ['input'],
  data() {
    return {
      state: this.$props.value
    };
  },
  methods: {
    mergeTooltipWithDefaults(options) {
      const opts = typeof options == 'string' ? {
        text: options
      } : options;
      return {
        location: "start top",
        ...opts
      };
    }
  },
  watch: {
    'state'(value) {
      this.$emit('update', value);
    }
  },
  computed: {
    miniVariant: {
      set(value) {
        this.state = value;
      },
      get() {
        return this.state && !this.$props.rail;
      }
    },
    hasChildren() {
      return this.$props.children?.length > 0;
    }
  }
};